'use strict';

/**
 *  event-bus 
 * @module EventBus
 */
class Bus {
  /**
   * @constructor
   */
  constructor () {
    if (Bus.__instance) {
      return Bus.__instance;
    }
    this.listeners = {};
    Bus.__instance = this;
  }

  /**
   *   
   * @param {string} event -  
   * @param {function} listener -  
   * @return {function(this:Bus)} -    
   */
  on (event, listener) {
    this.listeners[event] = this.listeners[event] || [];
    this.listeners[event].push(listener);
    return () => {
      this.off(event, listener);
    };
  }

  /**
   *   
   * @param {string} event -  
   * @param {function} listener -  
   */
  off (event, listener) {
    if (Array.isArray(this.listeners[event])) {
      this.listeners[event] = this.listeners[event]
        .filter((element) => {
          return element !== listener;
        });
    }
  }

  /**
   *  
   * @param {string} event -  
   * @param {*} [payload] -   
   */
  emit (event, payload) {
    if (Array.isArray(this.listeners[event])) {
      this.listeners[event].forEach((listener) => {
        listener({
          event: event,
          payload: payload
        });
      });
    }
  }
}
export default new Bus();